import React from 'react'
import { login, resetPassword, logout, saveOrganization } from '../auth'
import {getFunName, checkData} from '../helpers'
import base from '../base'

class Login extends React.Component {
  constructor() {
    super();
    this.state = { 
      loginMessage: null,
      listInfo: null,
      userList: null
    }
    this.handleSubmit = this.handleSubmit.bind(this);
    this.resetPassword = this.resetPassword.bind(this);
    this.setErrorMsg = this.setErrorMsg.bind(this);
    this.logOut = this.logOut.bind(this);

    this.getOrganization = this.getOrganization.bind(this);
    this.getUsers = this.getUsers.bind(this);

    this.renderLogin = this.renderLogin.bind(this);
    this.renderStoreInput = this.renderStoreInput.bind(this);

    this.goToStore = this.goToStore.bind(this);
    this.goToRegister = this.goToRegister.bind(this);
    this.goToExistingStore = this.goToExistingStore.bind(this);

  }

  componentDidMount () { 
    if(this.props.authed) {
      this.getOrganization()
      this.getUsers()
      return this.setState({ listLoaded: true })
    }
  }
  
  handleSubmit(e){
    e.preventDefault()
    login(this.email.value, this.pw.value)
    .catch((error) => {
      this.setState(this.setErrorMsg('Invalid username/password.'))
    })
  }
  resetPassword(){
    resetPassword(this.email.value)
      .then(() => this.setState(this.setErrorMsg(`Password reset email sent to ${this.email.value}.`)))
      .catch((error) => this.setState(this.setErrorMsg(`Email address not found.`)))
  }
  setErrorMsg(error) {
    return {
      loginMessage: error
    }
  }

  getOrganization(){
    base.fetch('organization', {
      context: this,
      asArray: true
    }).then(data => {
      this.setState({
        listInfo: data
      })
    }).catch(error => {
      return error
    })
  }

  getUsers(){
    base.fetch('users', {
      context: this,
      asArray: true
    }).then(data => {
      let userData = data;
      data.map((data)=>{
        const {userType} = data.info;
        if(userType !== 'admin') {
          console.log(data)
          this.setState({
            userList: userData
          })
        }
      })
      // this.setState({
      //   listInfo: data
      // })
    }).catch(error => {
      return error
    })
  }

  logOut() {
    logout()
    this.setState({loginMessage: null})
  }

  goToStore(e) {
    e.preventDefault();
    const storeId = this.storeInput.value;
    saveOrganization(storeId, this.props.user)
    this.context.router.transitionTo(`/organization/${storeId}`);
  }

  goToRegister(e) {
    e.preventDefault();
    this.context.router.transitionTo(`/register`);
  }

  goToExistingStore(storeId) {
    this.context.router.transitionTo(`/organization/${storeId}`);
  }

  renderOrganizationList(data) {
    if(checkData(data)) {
      let goToExistingStore = this.goToExistingStore;
      let listOrganization = data.map(
        function(data){      
          const { name } = data.info;
          return <h2 key={data.key} onClick={(data) => {goToExistingStore(name)}}>{name}</h2>
        }
      )
      return(
        <div>
          <h1>List of Organizations</h1>
          <ul>
            {listOrganization}
          </ul>
        </div>
      )
    }
    return <h1>Loading</h1>
  }

  renderUsersList(){

  }

  renderStoreInput() {
    return(
      <form action="" className="store-selector" onSubmit={this.goToStore}>
        <h2>Please Input your Organization</h2>
        <input type="text" required placeholder="Store Name" defaultValue={getFunName()} ref={(input)=>{this.storeInput = input}}/>
        <button type="submit">Visit Store</button>
      </form>
    )
  }

  renderLogin() {
    return (
      <div className="login">
        <h1> Login </h1>
        <form onSubmit={this.handleSubmit}>
          <div className="form-group">
            <label>Email</label>
            <input className="form-control" ref={(email) => this.email = email} placeholder="Email"/>
          </div>
          <div className="form-group">
            <label>Password</label>
            <input type="password" className="form-control" placeholder="Password" ref={(pw) => this.pw = pw} />
          </div>
          {
            this.state.loginMessage &&
            <div className="alert alert-danger" role="alert">
              <span className="glyphicon glyphicon-exclamation-sign" aria-hidden="true"></span>
              <span className="sr-only">Error:</span>
              &nbsp;{this.state.loginMessage} <a href="#" onClick={this.resetPassword} className="alert-link">Forgot Password?</a>
            </div>
          }
          <button type="submit" name="login" className="btn btn-primary">Login</button>
          <button type="button" name="regeister" className="btn btn-primary" onClick={this.goToRegister}>Register</button>
        </form>

      </div>
    )
  }

  render () {
    if(this.props.authed) {
      if(this.props.userLevel === 'user') {
        return (
          <div>
            <button onClick={this.logOut}>Log Out</button>
            {this.renderOrganizationList(this.state.listInfo)} 
          </div>
        )
      } else if(this.props.userLevel === 'admin') {
        return (
          <div>
            <button onClick={this.logOut}>Log Out</button>
            {this.renderStoreInput()}
            {this.renderOrganizationList(this.state.listInfo)} 
            {this.renderUsersList(this.state.listUser)}
          </div>
        )
      }
    }
    return <div>{this.renderLogin()}</div>
  }
}

Login.contextTypes = {
  router: React.PropTypes.object
}

export default Login;


